@import url(https://fonts.googleapis.com/css2?family=Staatliches&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  /* font-family: "Staatliches", cursive; */
  display: -webkit-flex;
  display: flex;
  text-align: center;
  width: 60%;
  margin: auto;
  margin-top: 50px;
}

#btn {
  width: 10%;
  margin: auto;
  margin-top: 20px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: center;
          align-items: center;
  text-align: center;
  border: 1px black solid;
  border-radius: 10px;
  padding: 1rem;
}

#btn div {
  width: 50%;
}

.card {
  width: 40%;
  margin: auto;
  display: block;
  border: 1px solid black;
  border-radius: 10px;
  padding: 1rem;
  margin-left: 5px;
  margin-right: 5px;
}

#controls {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
}

#controls div {
  width: 30%;
}

#controls button {
  width: 30%;
  border: none;
  height: 50px;
}

#timer {
  display: block;
}

#break-length {
  font-size: 2rem;
}

#session-length {
  font-size: 2rem;
}

#time-left {
  font-size: 2rem;
}

